
import { Component, Vue } from 'vue-property-decorator'
import Info from '@/components/LNL/footer.vue';

@Component({
  name: "Object",
  components: {
  }
})
export default class Object extends Vue {
  objects = [
    {
      link: require('@/assets/images/object_a/object_1.jpg'),
      title: 'Story I',
    },
    {
      link: require('@/assets/images/object_a/object_2.jpg'),
      title: 'Story Ⅱ',
    },
    {
      link: require('@/assets/images/object_a/object_3.jpg'),
      title: 'Story Ⅲ',
    },
    { link: require('@/assets/images/object_a/object_4.jpg') },
    { link: require('@/assets/images/object_a/object_5.jpg') },
    { link: require('@/assets/images/object_a/object_6.jpg') },
    { link: require('@/assets/images/object_a/object_7.jpg') },
    { link: require('@/assets/images/object_a/object_8.jpg') },
    { link: require('@/assets/images/object_a/object_9.jpg') }
  ]
  showNumber = 6
  toggle_exclusive = undefined
  show = false

  moreShow() {
    let add = (this.objects.length - this.showNumber) / 3
    if (add != 0) {
      this.showNumber += 4
    } else {
      this.showNumber += (this.objects.length - this.showNumber) % 3
    }
  }

  toggleShow() {
    this.show = !this.show;
  }
}
